.showcase-widget {
  display: inline-block;
  padding: 30px 30px;
  width: calc(200px + 10vw);
  height: calc(200px + 10vw);
  margin: 5vh 2vw;
  border-radius: 50px;
  background: #1f1f1f;
  box-shadow: calc(10px + 0.5vw) calc(10px + 0.5vw) calc((200px + 10vw) / 12) #171717, calc(-10px - 0.5vw) calc(-10px - 0.5vw) calc((200px + 10vw) / 12) #272727;
}
.showcase-desc {
  font-size: calc(13px + 1vw);
}
.showcase-button {
  margin-left: auto;
  border: 0;
  align-self: stretch;
  width: 100px;
  height: 60px;
  background: #77da77;
  border-radius: 25px;
  transition: 0.35s;
}
.showcase-button:hover {
  background: rgb(95, 174, 95);
  transition: 0.35s;
}
footer,header  {
  height: 10vh;
}
.showcase-flex {
  height: 100%;
  width: 100%;
  justify-content: space-between;
  flex-direction: column;
  display: flex;
}
a {
  color:#77da77;
}
a:visited {
  color: rgb(71, 130, 71);
}